.servicesContainer .box .contentText {
    position: relative;
    z-index: 1;
    transition: 0.5s;
}

.servicesContainer .box .contentText:hover {
    color: white;
}
.servicesContainer .box .contentText h5 {
    margin: 10px 0;
    padding: 0;
}

.servicesContainer .box .contentText p {
    margin: 0;
    padding: 0;
}
.servicesContainer .box .contentText a {
    display: inline-block;
    padding: 10px 20px;
    background-color: #fff;
    border-radius: 4px;
    text-decoration: none;
    color: #000;
    font-weight: 500;
    margin-top: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
}
.services h2 {
    border: 3px solid #03fa14;
    border-radius: 30px;
    width: 8em;
    text-align: center;
    -webkit-box-shadow: 10px 9px 21px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 9px 21px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 9px 21px 0px rgba(0, 0, 0, 0.75);
}
